import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

const router = new Router({
  routes: [
    {
      path: '/',
      name: 'default',
      component: () => import('./views/user/Index.vue')
    },
    {
      // 登录
      path: '/sign_in',
      name: 'signIn',
      // 路由级别的代码分割
      // 这将为这个路由生成一个单独的块(SignIn.[hash].js)
      // 当路由选中激活时懒加载此组件
      component: () => import(/* webpackChunkName: "login" */ './views/user/SignIn.vue')
    },
    {
      // 注册
      path: '/sign_up',
      name: 'signUp',
      component: () => import(/* webpackChunkName: "login" */ './views/user/SignUp.vue')
    },
    {
      // 用户中心首页
      path: '/user/index',
      name: 'user',
      component: () => import('./views/user/Index.vue')
    },
    {
      // 订单列表
      path: '/order/list',
      name: 'orderList',
      component: () => import('./views/order/List.vue')
    },
    {
      // 订单详情
      path: '/order/detail/:id',
      name: 'orderDetail',
      component: () => import('./views/order/Detail.vue')
    },
    {
      // 客户列表
      path: '/customer/list',
      name: 'customerList',
      component: () => import('./views/customer/List.vue')
    },
    {
      // 客户详情
      path: '/customer/detail/:id',
      name: 'customerDetail',
      component: () => import('./views/customer/Detail.vue')
    },
    {
      // 客户信息添加
      path: '/customer/add',
      name: 'customerAdd',
      component: () => import('./views/customer/Add.vue')
    },
    {
      // 客户订单列表
      path: '/customer/orders',
      name: 'customerOrders',
      component: () => import('./views/customer/Orders.vue')
    },
    {
      path: '*',
      name: 'page404',
      component: () => import('./views/error/404.vue')
    }
  ]
})

export default router